import React, { useEffect, useState } from "react";
import Loading from "components/common/loading/Loading";
import chuDeAPI from "api/admin/chuDe.api";
import { useParams } from "react-router";
import TableBasic from "components/common/TableBasic/TableBasic";
import i18n from "i18n/i18n";

const SyllabusPlanTheory = ({ idSyllabus }) => {
    const [syllabusPlan, setSyllabusPlan] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const params = useParams();
    useEffect(() => {
        (async () => {
            setIsLoading(true);
            const results = await chuDeAPI.getAll({
                idSyllabus,
                idLKHGD: 1,
            });
            if (results?.contents?.length) {
                const newSyllabusPlan = results?.contents
                    ?.map((item) => {
                        const hoatDongDayHocStr =
                            item?.hoatDongDayHoc
                                ?.map((data) => data?.ten)
                                .join("\n ") || "";
                        return { ...item, hoatDongDayHocStr };
                    })
                    .map((item) => {
                        const chuanDauRaMonHocStr =
                            item?.chuanDauRaMonHoc
                                ?.map((data) => data?.ma)
                                .join("\n ") || "";
                        return { ...item, chuanDauRaMonHocStr };
                    })
                    .map((item) => {
                        const hoatDongDanhGiaStr =
                            item?.hoatDongDanhGia
                                ?.map((data) => data?.ma)
                                .join("\n ") || "";
                        return { ...item, hoatDongDanhGiaStr };
                    });
                setSyllabusPlan(newSyllabusPlan);
            } else {
                setSyllabusPlan([]);
            }

            setIsLoading(false);
        })();
    }, []);
    const titleList = [
        { title: i18n.t("syllabus.TOPIC_NAME"), field: "ten" },
        {
            title: i18n.t("syllabus.STANDARD_OUTPUT"),
            field: "chuanDauRaMonHocStr",
        },
        {
            title: i18n.t("syllabus.LEARNING_ACTIVITY"),
            field: "hoatDongDayHocStr",
        },
        {
            title: i18n.t("syllabus.EVALUATION_ACTIVITY"),
            field: "hoatDongDanhGiaStr",
        },
    ];
    return (
        <>
            {isLoading && <Loading />}
            <div className="vertab-accordion panel-group">
                {!isLoading && (
                    <div className="panel-body">
                        <div className="basic_content">
                            {syllabusPlan?.length ? (
                                <TableBasic
                                    titleList={titleList}
                                    data={syllabusPlan}
                                />
                            ) : (
                                i18n.t("EMPTY")
                            )}
                        </div>
                    </div>
                )}
            </div>
        </>
    );
};

export default SyllabusPlanTheory;
