import i18n from "i18n/i18n";
import React, { useEffect, useState } from "react";
const menus = [
    {
        title: i18n.t("syllabus.sidebar.BASIC"),
        href: "#basic",
        key: "basic",
    },
    {
        title: i18n.t("syllabus.sidebar.DESCRIPTION"),
        href: "#description",
        key: "description",
    },
    {
        title: i18n.t("syllabus.sidebar.GOALS"),
        href: "#goals",
        key: "goals",
    },
    {
        title: i18n.t("syllabus.sidebar.STANDARD_OUTPUT"),
        href: "#standard-output",
        key: "standard-output",
    },
    {
        title: i18n.t("syllabus.sidebar.THEORY_PLAN"),
        href: "#plan-theory",
        key: "plan-theory",
    },
    {
        title: i18n.t("syllabus.sidebar.PRACTICE_PLAN"),
        href: "#plan-practices",
        key: "plan-practices",
    },
    // { title: i18n.t('syllabus.sidebar.EVALUATION'), href: "#evaluation", key: 'evaluation' },
    {
        title: i18n.t("syllabus.sidebar.RESOURCES"),
        href: "#resources",
        key: "resources",
    },
    {
        title: i18n.t("syllabus.sidebar.RULES"),
        href: "#rules",
        key: "rules",
    },
];
const SidebarSyllabus = ({ handleClickChooseTabIndex, tabIndex }) => {
    return (
        <div className="sidebar__container vertab-container">
            <div className="vertab-menu">
                <div className="list-group">
                    {menus?.map((item, index) => (
                        <a
                            key={index}
                            href={`${item?.href}`}
                            onClick={() => handleClickChooseTabIndex(item?.key)}
                            className={`list-group-item text-left ${
                                item?.key === tabIndex ? "active" : ""
                            }`}
                        >
                            {item?.title}
                        </a>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default SidebarSyllabus;
