import React, { useEffect, useState } from "react";
import Loading from "components/common/loading/Loading";
import chuanDauRaMonHocAPI from "api/admin/chuanDauRaMonHoc.api";
import { useParams } from "react-router";
import TableBasic from "components/common/TableBasic/TableBasic";
import i18n from "i18n/i18n";

const SyllabusStandardOutput = ({ idSyllabus }) => {
    const [syllabusOutput, setSyllabusOutput] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    useEffect(() => {
        (async () => {
            setIsLoading(true);
            const results = await chuanDauRaMonHocAPI.getAll({
                idSyllabus,
            });
            if (results?.contents?.length) {
                setSyllabusOutput(results?.contents);
            } else {
                setSyllabusOutput([]);
            }

            setIsLoading(false);
        })();
    }, []);
    const titleList = [
        { title: i18n.t("syllabus.STANDARD_OUTPUT"), field: "ma" },
        { title: i18n.t("syllabus.DESCRIPTION"), field: "mota" },
        { title: i18n.t("syllabus.LEVEL"), field: "mucDo" },
    ];
    return (
        <>
            {isLoading && <Loading />}
            <div className="vertab-accordion panel-group">
                {!isLoading && (
                    <div className="panel-body">
                        <div className="basic_content">
                            {syllabusOutput?.length ? (
                                <TableBasic
                                    titleList={titleList}
                                    data={syllabusOutput}
                                />
                            ) : (
                                i18n.t("EMPTY")
                            )}
                        </div>
                    </div>
                )}
            </div>
        </>
    );
};

export default SyllabusStandardOutput;
