import React, { useEffect, useState } from "react";
import Loading from "components/common/loading/Loading";
import mucTieuMonHocAPI from "api/admin/mucTieuMonHoc.api";
import TableBasic from "components/common/TableBasic/TableBasic";
import i18n from "i18n/i18n";

const SyllabusGoals = ({ idSyllabus }) => {
    const [syllabusGoals, setSyllabusGoals] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    useEffect(() => {
        (async () => {
            setIsLoading(true);
            const results = await mucTieuMonHocAPI.getAll({
                idSyllabus,
            });
            if (results?.contents?.length) {
                const newSyllabusGoals = results?.contents?.map((item) => {
                    const cDRCDIO =
                        item?.chuanDauRaCDIO
                            ?.map((data) => data?.ma)
                            .join(", ") || "";
                    return { ...item, cDRCDIO };
                });
                setSyllabusGoals(newSyllabusGoals);
            } else {
                setSyllabusGoals([]);
            }

            setIsLoading(false);
        })();
    }, []);
    const titleList = [
        { title: i18n.t("GOALS"), field: "ma" },
        { title: i18n.t("DESCRIPTION"), field: "moTa" },
        { title: i18n.t("CƒêR_CDIO"), field: "cDRCDIO" },
    ];
    return (
        <>
            {isLoading && <Loading />}
            <div className="vertab-accordion panel-group">
                {!isLoading && (
                    <div className="panel-body">
                        <div className="basic_content">
                            {syllabusGoals?.length ? (
                                <TableBasic
                                    titleList={titleList}
                                    data={syllabusGoals}
                                />
                            ) : (
                                i18n.t("EMPTY")
                            )}
                        </div>
                    </div>
                )}
            </div>
        </>
    );
};

export default SyllabusGoals;
