import React, { useEffect, useState } from "react";
import Loading from "components/common/loading/Loading";
import syllabusAPI from "api/admin/syllabus.api";
import i18n from "i18n/i18n";
import { isNotNull } from "utils/utils";

import "./Syllabus_Basic.css";
const SyllabusBasic = ({ idSyllabus }) => {
    const [syllabus, setSyllabus] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    useEffect(() => {
        (async () => {
            setIsLoading(true);
            const result = await syllabusAPI.getById(idSyllabus);
            setSyllabus(result);
            setIsLoading(false);
        })();
    }, [idSyllabus]);
    return (
        <>
            {isLoading && <Loading />}
            <div className="vertab-accordion panel-group">
                {!isLoading && syllabus &&(
                    <div className="panel-body">
                        <div className="syllabus__top">
                            <p className="syllabus__title__top">
                                {i18n.t("syllabus.OUTLINE_SUBJECT")}
                            </p>
                            <h2 className="syllabus__name">
                                {`${syllabus?.monHoc?.ma} - ${syllabus?.monHoc?.tenTiengViet}`}
                            </h2>
                        </div>
                        <div className="basic_content">
                            <div className="basic__label">
                                <p>{`${i18n.t(
                                    "syllabus.SUBJECT_NAME"
                                )} (VN)`}</p>
                                <p>{`${i18n.t(
                                    "syllabus.SUBJECT_NAME"
                                )} (EN)`}</p>
                                <p>{i18n.t("syllabus.SUBJECT_CODE")}</p>
                                <div className="level-1">
                                    <p>{i18n.t("syllabus.SUBJECT_CREDIT")}</p>
                                    <div className="level-2">
                                        <p>
                                            {i18n.t(
                                                "syllabus.NUMBER_OF_THEORETICAL_LESSONS"
                                            )}
                                        </p>
                                        <p>
                                            {i18n.t(
                                                "syllabus.NUMBER_OF_PRACTICE_LESSONS"
                                            )}
                                        </p>
                                        <p>
                                            {i18n.t(
                                                "syllabus.NUMBER_OF_SELF_STUDY_PERIODS"
                                            )}
                                        </p>
                                    </div>
                                </div>
                                <p>
                                    {i18n.t("syllabus.PREREQUISITE_SUBJECTS")}
                                </p>
                            </div>
                            <div className="basic__contents">
                                <p>
                                    {isNotNull(
                                        syllabus?.monHoc?.tenTiengViet
                                    ) || ""}
                                </p>
                                <p>
                                    {isNotNull(syllabus?.monHoc?.tenTiengAnh) ||
                                        "  "}
                                </p>
                                <p>{isNotNull(syllabus?.monHoc?.ma) || ""}</p>
                                <p>{syllabus?.monHoc?.soTinChi}</p>
                                <p>{syllabus?.monHoc?.soTietLyThuyet}</p>
                                <p>{syllabus?.monHoc?.soTietThucHanh}</p>
                                <p>{syllabus?.monHoc?.soTietTuHoc}</p>
                                <p>
                                    {isNotNull(
                                        syllabus?.monHoc?.monHocTienQuyet
                                            ?.map(
                                                (item) => item?.monHocTruoc?.ma
                                            )
                                            ?.join(", ")
                                    ) || ""}
                                </p>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </>
    );
};

export default SyllabusBasic;
