import axiosClient from "api/common/axiosClient";
import CONFIG from "config/config";
import queryString from "query-string";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";

const syllabusAPI = {
  getAll: async (filter = {}) => {
    const query = queryString.stringify({ limit:-1,page:0,...filter });
    try {
      const url = `${CONFIG.API.SYLLABUS}?${query}`;
      return await axiosClient.get(url);
    } catch (error) {
      toast.error(i18n.t("ERROR"));
    }
  },
  getById: async (id = "") => {
    try {
      if (id) {
        const url = `${CONFIG.API.SYLLABUS}/${id}`;
        return await axiosClient.get(url);
      }
      toast.error(i18n.t('syllabus.SYLLABUS_INVALID'));
    } catch (error) {
      toast.error(i18n.t("ERROR"));
    }
  },
};

export default syllabusAPI;
