import React from "react";
import { Table, Button } from "reactstrap";
import * as lodash from "lodash";
import i18n from "i18n/i18n";

const TableBasic = ({ titleList = [], data = [], actionTitle = "" }) => {
    return (
        <div>
            <Table bordered>
                <thead>
                    <tr>
                        <th>STT</th>
                        {titleList?.map((item, index) => (
                            <th key={index}>{item?.title}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {data?.map((item, index) => (
                        <tr key={index}>
                            <th scope="row">{index}</th>
                            {titleList?.map((titleItem) => (
                                <td key={`${index}-${titleItem?.field}`}>
                                    {titleItem?.field ? (
                                        lodash.get(item, titleItem?.field, "")
                                    ) : (
                                        <Button
                                            color="primary"
                                            size="sm"
                                            onClick={() =>
                                                titleItem?.handle({
                                                    ...item,
                                                })
                                            }
                                        >
                                            {titleItem?.actionTitle ||
                                                i18n.t(
                                                    actionTitle || "PREVIEW_PDF"
                                                )}
                                        </Button>
                                    )}
                                </td>
                            ))}
                        </tr>
                    ))}
                </tbody>
            </Table>
        </div>
    );
};

export default TableBasic;
